<template>
  <div class="report">
    <h3>Журнал</h3>
    <Loader v-if="loading" />
    <div v-show="!loading" class="row width">
      <div class="input-field col s5">
        <input
          type="text"
          autocomplete="off"
          id="first-datepicker"
          ref="first-datepicker"
        />
        <label for="first-datepicker">Выбери начало</label>
      </div>
      <div class="input-field col push-s2 s5">
        <input
          type="text"
          autocomplete="off"
          id="second-datepicker"
          ref="second-datepicker"
        />
        <label for="second-datepicker">Выбери конец</label>
      </div>
    </div>
    <button
      v-show="!loading"
      @click="loadJournal"
      class="btn light-blue darken-1 waves-effect waves-light"
    >
      Загрузить
    </button>
    <div v-show="!loading" id="report" v-html="report"></div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader.vue";

export default {
  data: () => ({
    report: "",
    loading: !0,
    instances: []
  }),
  methods: {
    ...mapActions(["GetRange", "GetJournal"]),
    init(data) {
      let range = {};
      try {
        range = JSON.parse(localStorage["vi-sgo-report"]);
      } catch (e) {
        localStorage["vi-sgo-report"] = "{}";
      }
      const datepickerOptions = {
        format: "dd.mm.yy",
        firstDay: 1,
        minDate: new Date(data.start),
        maxDate: new Date(data.end),
        i18n: {
          cancel: "Отмена",
          done: "Готово",
          months: [
            "Январь",
            "Февраль",
            "Март",
            "Апрель",
            "Май",
            "Июнь",
            "Июль",
            "Август",
            "Сентябрь",
            "Октябрь",
            "Ноябрь",
            "Декабрь"
          ],
          monthsShort: [
            "Янв",
            "Фев",
            "Мар",
            "Апр",
            "Май",
            "Июн",
            "Июл",
            "Авг",
            "Сен",
            "Окт",
            "Ноя",
            "Дек"
          ],
          weekdays: [
            "Воскресенье",
            "Понедельник",
            "Вторник",
            "Среда",
            "Четверг",
            "Пятница",
            "Суббота"
          ],
          weekdaysShort: ["Вск", "Пнд", "Втр", "Срд", "Чтв", "Птн", "Сбт"],
          weekdaysAbbrev: ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"]
        },
        setDefaultDate: true
      };
      setTimeout(() => {
        this.destroy();
        this.instances.push(
          // eslint-disable-next-line no-undef
          M.Datepicker.init(this.$refs["first-datepicker"], {
            ...datepickerOptions,
            defaultDate: new Date(range?.start)
          }),
          // eslint-disable-next-line no-undef
          M.Datepicker.init(this.$refs["second-datepicker"], {
            ...datepickerOptions,
            defaultDate: new Date(range?.end)
          })
        );
      }, 0);
    },
    destroy() {
      for (let instance of this.instances) {
        instance?.destroy?.();
      }
      this.instances = [];
    },
    saveRange(range) {
      localStorage["vi-sgo-report"] = JSON.stringify(range);
      return range;
    },
    loadJournal() {
      this.loading = true;
      const [start, end] = this.instances;
      const range = this.saveRange({
        start: start.toString("yyyy-mm-dd"),
        end: end.toString("yyyy-mm-dd")
      });
      this.GetJournal(range)
        .then(html => ((this.loading = false), (this.report = html)))
        .catch(err => {
          this.loading = false;
          if (err.code == 4008) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Сайт лег, попробуй позже" });
          } else if (err.code == 4006) {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
          } else {
            // eslint-disable-next-line no-undef
            M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
          }
          console.error(err);
        });
    }
  },
  created() {
    this.GetRange()
      .then(range => this.init(range))
      .then(() => (this.loading = false))
      .catch(err => {
        this.loading = false;
        if (err.code == 4008) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Сайт лег, попробуй позже" });
        } else if (err.code == 4006) {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Ошибка парсера, попробуй ещё раз" });
        } else {
          // eslint-disable-next-line no-undef
          M.toast({ html: "Неизвестная ошибка, попробуй ещё раз" });
        }
        console.error(err);
      });
  },
  destroyed() {
    this.destroy();
  },
  components: {
    Loader
  }
};
</script>
